import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import Home from "./Home";
import './styles.css';
import Redirect from "./Redirect";

function App() {
  return (
    <div>
      <a href="https://breloom.xyz/"><img src="./breloom.png" id="homebutton" /></a>
      <Router>
        <div>
          <Switch>
            <Route path="/">
              <Home />
            </Route>
            <Route
            path="/privacy-policy"
            component={ Redirect }
            loc="https://hahs.sentral.com.au/portal2/"
            />
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default App;